:root {
    --navBg: #0c0c0c;
  }
  
  .navbar {
    background-color: var(--navBg);
    height: 60px;
    display: flex;
    justify-content: start;
    align-items: center;
  }
  
  .menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;

  }
  
  .nav-menu {
    background-color: var(--navBg);
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position:absolute;
    top: 0;
    left: -100%;
    transition: 850ms;

  }
  
  .nav-menu.active {
    left: 0;
    transition: 350ms;
    
  }
  
  .nav-text {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0 8px 16px;
    list-style: none;
    height: 60px;

  }
  
  .nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 16px;
    border-radius: 4px;
    

  }
  
  .nav-text a:hover {
    background-color: #1a83ff;
    

  }
  
  .nav-menu-items {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    align-items: center;
    padding: 0;
    margin: 0;
  }
  
  .nav-menu-items li {
    width: 100%;
    margin: 10px 0;
    list-style: none;
    display: flex; /* Add this */
    align-items: center; /* Add this */
  }
  
  .nav-menu-items li span {
    margin-left: 10px; /* Adjust this margin as needed */
  }
  
  .navbar-toggle {
    background-color: var(--navBg);
    width: 100%;
    height: 90px;

    display: flex;
    justify-content: start;
    align-items: center;
  }
  
  
  .titleNav{
    padding-left: 30;
 
    color:rgb(199, 196, 196);
  }